<template>
  <div>
    <Layout class :style="{marginLeft: '200px'}">
      <Content :style="{padding: '0 16px 16px'}">
        <Breadcrumb :style="{margin: '16px 0'}">
          <BreadcrumbItem></BreadcrumbItem>
          <BreadcrumbItem></BreadcrumbItem>
          <BreadcrumbItem>Layout</BreadcrumbItem>
        </Breadcrumb>
        <Card>
          <Form ref="php" :model="php" :rules="ruleValidate" label-position="top">
            <FormItem prop="input1" label="">
              <Input ref="dayInput" id="dayInput" v-model="php.input2" />
            </FormItem>
            <FormItem prop="input1" label="">
              <Input ref="dayInput" id="dayInput" v-model="php.input3" />
            </FormItem>
            <div v-for="(d,index) in dayContent" :key="index">
              <FormItem prop="input1" label="">
                <Input ref="dayInput" id="dayInput" />
              </FormItem>
            </div>
            {{ php.input1 }}
            <Icon type="md-flash" @click="dayInputAdd" class="fs-25 cursor-pointer" />
            <FormItem>
              <Button class="pull-left" @click="save('php')" type="info"></Button>
              <Button class="pull-left m-l-20" @click="monthSubmit" type="success"></Button>
              <Modal
                @on-ok="dayok"
                @on-cancel="dayCancel"
                title=""
                v-model="modal7"
                :closable="false"
              >
                <div style="border-bottom: 1px solid #e9e9e9;padding-bottom:6px;margin-bottom:6px;">
                  <div class="text-blue flex text-center">
                    <div class="flex-1">:{{ dayName }}</div>
                    <div class="flex-1 b-h">:{{ userName }}</div>
                    <div class="flex-1">{{dateTime}}</div>
                  </div>
                </div>
                <div style="border-bottom: 1px solid #e9e9e9;padding-bottom:6px;margin-bottom:6px;">
                  <Checkbox
                    :indeterminate="indeterminate"
                    :value="checkAll"
                    @click.prevent.native="handleCheckAll"
                  ></Checkbox>
                </div>
                <CheckboxGroup v-model="checkAllGroup" @on-change="checkAllGroupChange">
                  <Checkbox label=""></Checkbox>
                  <Checkbox label=""></Checkbox>
                  <Checkbox label=""></Checkbox>
                  <Checkbox label=""></Checkbox>
                </CheckboxGroup>
                <!--    <div style="border-bottom: 1px solid #e9e9e9;padding-bottom:6px;margin-bottom:6px;">
                <Button type="primary"></Button>
                <Button type="primary"></Button>
                </div>-->
              </Modal>
              <Button class="pull-left m-l-20" @click="cancel('php')" type="error"></Button>
            </FormItem>
          </Form>
        </Card>
      </Content>
    </Layout>
  </div>
</template>
<script>
export default {
  data() {
    return {
      dayContent: [],
      dayName: "",
      userName: "",
      dateTime: "",
      modal7: false,
      indeterminate: true,
      checkAll: false,
      checkAllGroup: ["", "", "", ""],
      user: [{ name: "" }, { name: "" }, { name: "" }],
      php: {
        input1: ""
        /*   input2: "",
        input3: "",
        input4: "",
        input5: "",
        input6: "" */
      },
      ruleValidate: {
        input1: [
          {
            trigger: "blur"
          }
        ]
        /* input2: [
          {
            trigger: "blur"
          }
        ],
        input3: [
          {
            trigger: "blur"
          }
        ],
        input4: [
          {
            trigger: "blur"
          }
        ],
        input5: [
          {
            trigger: "blur"
          }
        ],
        input6: [
          {
            trigger: "blur"
          } 
        ]*/
      }
    };
  },
  methods: {
    dayInputAdd() {
      // alert("addInputAdd")
      this.dayContent.push({});
    },
    dayok() {
      this.$Message.info("");
    },
    dayCancel() {
      this.$Message.info("");
    },
    handleCheckAll() {
      if (this.indeterminate) {
        this.checkAll = false;
      } else {
        this.checkAll = !this.checkAll;
      }
      this.indeterminate = false;

      if (this.checkAll) {
        this.checkAllGroup = ["", "", "", ""];
      } else {
        this.checkAllGroup = [];
      }
    },
    checkAllGroupChange(data) {
      if (data.length === 4) {
        this.indeterminate = false;
        this.checkAll = true;
      } else if (data.length > 0) {
        this.indeterminate = true;
        this.checkAll = false;
      } else {
        this.indeterminate = false;
        this.checkAll = false;
      }
    },
    monthSubmit() {
      this.modal7 = true;
      var aData = new Date();
      this.value =
        aData.getFullYear() +
        "" +
        (aData.getMonth() + 1) +
        "" +
        aData.getDate() +
        "" +
        aData.getHours() +
        "" +
        aData.getMinutes() +
        "";

      this.dateTime = this.value;
    },
    save(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          this.$Message.success("");
        } else {
          this.$Message.error("");
        }
      });
    },

    cancel(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          this.$Message.success("");
        } else {
          this.$Message.error("");
        }
      });
    }
  }
};
</script>
<style lang="stylus" scoped></style>