<template>
  <div>
    <Layout class :style="{marginLeft: '200px'}">
      <Content :style="{padding: '0 16px 16px'}">
        <Breadcrumb :style="{margin: '16px 0'}">
          <BreadcrumbItem></BreadcrumbItem>
          <BreadcrumbItem></BreadcrumbItem>
          <BreadcrumbItem>Layout</BreadcrumbItem>
        </Breadcrumb>
        <Card>
          <Form ref="php" :model="php" :rules="ruleValidate" label-position="top">
            <FormItem class>
              <input class ref="textName" id="dayInputOutline" placeholder="" />
            </FormItem>
            <FormItem prop="input1" label>
              <p></p>
              <Input
                style="width:70%"
                class="m-r-20"
                ref="dayInput"
                id="dayInput"
                v-model="php.input2"
              />
              <!-- <Button @click="dayAnswer" type="info">{{dayButton}}</Button> -->
              <Radio label="apple">
                <Icon type="social-apple"></Icon>
                <span></span>
              </Radio>
            </FormItem>
            <FormItem prop="input1" label>
              <p ref="inout1"></p>
              <Input
                style="width:70%"
                class="m-r-20"
                ref="dayInput"
                id="dayInput"
                v-model="php.input3"
              />
              <!-- <Button @click="dayAnswer" type="info">{{dayButton}}</Button> -->
              <Radio label="apple">
                <Icon type="social-apple"></Icon>
                <span></span>
              </Radio>
            </FormItem>
            <div v-for="(d,index) in dayContent" :key="index">
              <p ref="dayAddInputNumber"></p>
              <FormItem prop="input1" label>
                <Input style="width:70%" class="m-r-20" ref="dayInput" id="dayInput" />
                <!-- <Button @click="dayAnswer" type="info">{{dayButton}}</Button> -->
                <Radio label="apple">
                  <Icon type="social-apple"></Icon>
                  <span></span>
                </Radio>
              </FormItem>
            </div>
            {{ php.input1 }}
            <Icon type="md-flash" @click="dayInputAdd" class="fs-25 cursor-pointer" />
            <FormItem>
              <Button class="pull-left" @click="save('php')" type="info"></Button>
              <Button class="pull-left m-l-20" @click="daySubmit" type="success"></Button>
              <Modal
                @on-ok="dayok"
                @on-cancel="dayCancel"
                title=""
                v-model="modal7"
                :closable="false"
              >
                <div style="border-bottom: 1px solid #e9e9e9;padding-bottom:6px;margin-bottom:6px;">
                  <div class="text-blue flex text-center">
                    <div class="flex-1">:{{ textName }}</div>
                    <div class="flex-1 b-h">:{{ userName }}</div>
                    <div class="flex-1">{{dateTime}}</div>
                  </div>
                </div>
                <div style="border-bottom: 1px solid #e9e9e9;padding-bottom:6px;margin-bottom:6px;">
                  <Checkbox
                    :indeterminate="indeterminate"
                    :value="checkAll"
                    @click.prevent.native="handleCheckAll"
                  ></Checkbox>
                </div>
                <CheckboxGroup v-model="checkAllGroup" @on-change="checkAllGroupChange">
                  <Checkbox label=""></Checkbox>
                  <Checkbox label=""></Checkbox>
                  <Checkbox label=""></Checkbox>
                  <Checkbox label=""></Checkbox>
                </CheckboxGroup>
              </Modal>
              <Button class="pull-left m-l-20" @click="cancel('php')" type="error"></Button>
            </FormItem>
          </Form>
        </Card>
      </Content>
    </Layout>
  </div>
</template>
<script>
export default {
  data() {
    return {
      dayAddInputNumber: "2",
      submitTable: false,
      dayContent: [],
      textName: "",
      userName: "",
      dateTime: "",
      modal7: false,
      indeterminate: true,
      checkAll: false,
      checkAllGroup: ["", "", "", ""],
      user: [{ name: "" }, { name: "" }, { name: "" }],
      php: {
        input1: ""
      },
      ruleValidate: {
        input1: [
          {
            trigger: "blur"
          }
        ]
      }
    };
  },
  methods: {
    dayAnswer() {
      // alert(this.$refs.inout1.innerHTML.split(""));
    },
    daySubmit() {
      this.textName = this.$refs.textName.value;
      this.modal7 = true;
      var aData = new Date();

      this.value =
        aData.getFullYear() +
        "" +
        (aData.getMonth() + 1) +
        "" +
        aData.getDate() +
        "" +
        aData.getHours() +
        "" +
        aData.getMinutes() +
        "";

      this.dateTime = this.value;
    },
    dayInputAdd() {
      this.dayContent.push({});
      this.dayAddInputNumber++;
      // alert(this.$refs.dayAddInputNumber.innerHTML)
    },

    dayok() {
      this.$Message.info("");
      this.submitTable = true;
      // alert(this.submitTable);
    },
    beforeRouteEnter(to, from, next) {
      console.log(to);
      console.log(this.submitTable);
    },
    BeforeRouteUpdate(to, from, next) {
      console.log(to);
    },
    BeforeRouteLeave(to, from, next) {
      console.log(to);
    },
    dayCancel() {
      this.$Message.info("");
    },
    handleCheckAll() {
      if (this.indeterminate) {
        this.checkAll = false;
      } else {
        this.checkAll = !this.checkAll;
      }
      this.indeterminate = false;

      if (this.checkAll) {
        this.checkAllGroup = ["", "", "", ""];
      } else {
        this.checkAllGroup = [];
      }
    },
    checkAllGroupChange(data) {
      // alert(data)
      if (data.length === this.user.length + 1) {
        this.indeterminate = false;
        this.checkAll = true;
      } else if (data.length > 0) {
        this.indeterminate = true;
        this.checkAll = false;
      } else {
        this.indeterminate = false;
        this.checkAll = false;
      }
    },
    save(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          this.$Message.success("");
        } else {
          this.$Message.error("");
        }
      });
    },

    cancel(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          this.$Message.success("");
        } else {
          this.$Message.error("");
        }
      });
    }
  }
};
</script>
<style lang="stylus" scoped></style>